.home-box {
    display: flex;
    align-items: flex-start;
    padding: 4rem;
    margin: 2rem;
    border: 1px solid #000;
    border-radius: 8px;
}

.left-section {
    flex: 1.2;
}

.right-section {
    display: flex;
    align-items: center;
    justify-content: center;
    flex: 1;
}

.header {
    text-align: left;
}

.small-font {
    font-size: 1.5rem;
}

h1 {
    font-size: 4rem;
    margin: 0;
}

h2 {
    font-size: 2.5rem;
    margin: 0;
}

p {
    font-size: 2rem;
    margin: 0;
}

.medium-font {
    font-size: 1.4rem;
}

.content {
    margin-top: 1rem;
}

.content ul {
    list-style-type: none;
    padding: 0;
    margin-top: 0.5rem;
}

.content li {
    margin-bottom: 0.5rem;
}

.avatar-box {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
}